* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.nav-container {
    width: 100%;
    max-height: 100%;
    display: flex;
    flex-direction: row;
}

.home-menu {
    border: 1px solid rgba(71, 57, 91, 0.805);
    background-color: rgba(152, 149, 160, 0.414);
    width: 100%;
    max-height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.home-menu .nav-menu {
    display: flex;
    width: 40%;
    min-height: 10vh;
    float: left;
    margin-right: 18vw;
    margin-left: 5vw;
}

.home-menu .nav-menu ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    align-items: center;
    text-decoration: none;
}

.home-menu .nav-menu ul li {
    display: flex;
    height: 100%;
    width: 8em;
    align-items: center;
    justify-content: center;
}

.home-menu .nav-menu ul li a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    color: black;
    text-decoration: none;
    font-family: Open Sans, sans-serif;
    text-transform: uppercase;
    font-weight: bold;
}

.home-menu .nav-menu ul li:hover a {
    color: rgb(45, 40, 40);
    font-size: large;
    font-weight: bolder;
}

.home-menu .nav-menu ul li:hover {
    background-color: rgba(94, 101, 109, 0.381);
}

.home-menu .welcome {
    font-size: 1.3em;
    text-shadow: 4px 3px 6px rgba(81, 63, 63, 0.462);
}

.container {
    width: 100vw;
    height: 75vh;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.container .side-nav {
    border: 1px solid black;
    width: 60vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.container .side-nav-chi {
    border: 1px solid rgb(29, 179, 92);
    width: 100%;
    height: 23%;
}

.container .main-page {
    border: 1px solid blue;
    width: 40vw; /* Đảm bảo chiều rộng của phần này không quá lớn */
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center; /* Căn giữa hình ảnh */
    position: relative; /* Để căn chỉnh các nút điều hướng */
}

.container .image-container {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* Ensure that the image does not overflow */
}

.container .image-container .banner-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center; /* Center the image */
}

.container .main-page .prev-btn,
.container .main-page .next-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 4em;
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.container .main-page .prev-btn {
    display: none;
    left: 0px; 
}

.container .main-page .next-btn {
    display: none;
    right: 0px;
}

.container .main-page:hover .prev-btn,
.container .main-page:hover .next-btn {
    display: flex;
}

.container .main-page .prev-btn:hover,
.container .main-page .next-btn:hover {
    background-color: rgba(218, 190, 190, 0.364);
}


.footer {
    border: 1px solid red;
    height: 100vh;
    width: 100%;
}
